import {useState,useEffect} from 'react';
import './App.css';
import MainContainer from './components/MainContainer'; 
import Header from './components/Header';
import ToDoBox from './components/ToDoBox';
import AddContainer from './components/AddContainer';
import ToDo from './components/ToDo';


function App() {

  
  const [todoList,setTodoList]=useState(['']);
  
   useEffect(()=>{
     if(localStorage.length<1){localStorage.setItem('todos','')}
     
    setTodoList(localStorage.getItem('todos')!.split(','))
    
  },[])
  
  useEffect(()=>{
    if(todoList.indexOf('')>-1){
      let listClone=[...todoList]
      listClone.splice(listClone.indexOf(''))
      setTodoList(listClone)
    }
  },[todoList])
  

  function addToDo(newTodo:string){
    const listClone = [...todoList];
    
    if(newTodo!==''){
    listClone.push(newTodo);
      }

    if(listClone.indexOf('')>-1){
      listClone.splice(listClone.indexOf('',1))
      }

    setTodoList(listClone);
    localStorage.setItem('todos',listClone.join(','))
    
   console.log(listClone)
  }

  function delToDo(index:number) {
    
    let listClone = [...todoList];

    listClone.splice(index,1);

    setTodoList(listClone);
    
    localStorage.setItem('todos',listClone.join(','))
    console.log(listClone)

  }

  return (
    <MainContainer>
      <Header/>
      <ToDoBox>
       {todoList.map((element,index)=>{return(
         <ToDo del={delToDo} chave={index}>{element}</ToDo>
        
       
       )})}
       
      </ToDoBox>  
      <AddContainer add={addToDo}/>
     
    </MainContainer>
    
  );
}

export default App;
